.login{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 100vh;
    gap: 2rem;
}

.login::before{
    display: block;
    content: '';
    background: url('../../Assets/login.jpg') no-repeat center center;
    background-size: cover;
}

.forms{
    max-width: 36rem;
    padding: 1rem;
    margin-top: 15vh;
}

@media (max-width:40rem) {
    .login{
        grid-template-columns: 1fr;
    }
    .login::before{
        display: none;
    }
    .forms{
        max-width: 100%;
    }
    
}