@import url('https://fonts.googleapis.com/css2?family=Spectral:wght@700&display=swap');
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    color: #333;
    --type-first: Helvetica, Arial, sans-serif;
    --type-second: 'Spectral', Georgia;
    font-family: var(--type-first);
    padding-top: 4rem;
}

ul, li{
    list-style: none;
}

img{
    display: block;
    /* para responsividade*/
    max-width: 100%;
}

button, input{
    display: block;
    font-size: 1rem;
    font-family: var(--type-first);
    color: #333;

}

a{
    text-decoration: none;
    color: #333;
}

.container{
    max-width: 50rem;
    padding: 0 1rem;
    margin: 0 auto;
}

.mainCointainer{
    margin-top: 2rem;
}

.title{
    font-family: var(--type-second);
    line-height: 1;
    font-size: 3rem;
    margin: 1rem 0;
    position: relative;
    z-index: 1;
}

.title::after{
    content: '';
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    background: #fb1;
    position: absolute;
    bottom: 5px;
    left: -5px;
    border-radius: 0.2rem;
    z-index: -1;


}

.anime-left{
    opacity: 0;
    transform: translateX(-20px);
    animation: anime-left 0.3s forwards;
}

@keyframes anime-left{
    to{
        opacity: 1;
        transform: initial
    }
}

.App{
    display: flex;
    flex-direction: column;
    min-height: calc(100vh + 10rem);

}
.AppBody{
    flex: 1;
}

.VictoryContainer{
    height: initial !important;
}